.App {
  text-align: center;
  height: 100vh;
}

.app-container{
  width: 100%;
  height: 100vh;
  background-image: url('./images/spaceBackground.png');
  background-size: cover;
  text-align: center;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

h1{
  display: inline;
  width: fit-content;
  margin: auto;
  background-color: aliceblue;
  color: rgb(58, 54, 63);
  padding: .6rem 1rem 1rem 1rem;
  border-radius: 1rem;
}

@keyframes spin{
  from{transform:rotate(0deg);}
  to {transform:rotate(360deg);}
}

.AsteroidImageContainer{
  margin: auto;
  height: 600px;
  display: flex;
  justify-content: center;
  padding: 0;
}

.AsteroidPivot{
  position: absolute;
  top: 50%;
  left: 50%;
  transform-origin: top left;
  animation: spin 3s linear infinite;
  padding-left: 150px;
}

.AsteroidImage{
  height: 25px;
}

.Flames{
  height: 25px;
  transform: rotate(270deg) translateX(100%);
}

.EarthContainer{
  position: relative;
  width: 100px;
  height: 100px;
  align-self: center;
}

.Earth{
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  animation: spin 70s linear infinite reverse;
}

.Clouds{
  position: relative;
  top: -105px;
  left: 2px;
  width: 100px;
  height: 100px;
}

.StatsContainer{
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Stats{
  display: inline;
  margin: 20px;
  height: inherit;
  border-radius: 20px;
  text-align: left;
  padding: 1rem;
  font-size: .8rem;
  border-collapse: collapse;
}

.Stats tr{
  line-height: 2rem;
  padding-top: 5px;
  border-top: 1px solid white;
  white-space: nowrap;
}


.Stats th{
  width: 50%;
}

.ChangeStats{
  display: inline-block;
  width: 3rem;
  height: 6rem;
  background-color: rgba(240, 248, 255, 0.774);
}

