}

.AsteroidsDisplay{
  display: none;
  width: min(800px, 100%);
  margin: auto;
  background-color: aliceblue;
  padding: 2rem;
  font-size: .8rem;
  border-radius: 1rem;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
